import React from 'react';

import styles from "./FormDef.module.scss"
import MainBtn from "../MainBtn/MainBtn";

function FormDef({
                     isMultipart,
                     onSubmit,
                     className,
                     errorProp,
                     successProp,
                     successText,
                     children,
                     loading
}) {

    return (
        <section className={`${styles["formDef"]} ${className ? className : ''} containerSmall`}>
            <form
                className={styles["formDef__form"]}
                encType={isMultipart ? 'multipart/form-data' : 'application/x-www-form-urlencoded'}
                onSubmit={onSubmit}
            >
                <div className={styles["formDef__main"]}>
                    {children}
                </div>
                <MainBtn
                    disabled={loading}
                    className={styles['formDef__btn']}
                    type='submit'
                >Send</MainBtn>
                <p className={`${styles["formDef__errorText"]} ${errorProp || successProp ? styles["formDef__errorText_active"] : ''} contentTextDef`}>
                    <span
                        className={`${errorProp ? styles["formDef__errorText_error"] : ''} ${successProp ?  styles["formDef__errorText_success"] : ''}`}
                    >{errorProp ? errorProp : successText}</span>
                </p>
            </form>
        </section>
    );
}

export default FormDef;