import {
    CLEAR_CONTACT_ERROR,
    CLEAR_CONTACT_MESSAGE,
    CONTACT_FAIL,
    CONTACT_LOADING_START,
    CONTACT_SUCCESS,
} from "../types";
import {contactUrl, fetchRequest, setError} from "./fetchTools";


export const contact = (data,onResetForm) => async (dispatch) => {
    dispatch(clearContactError())
    dispatch({type: CONTACT_LOADING_START})
    try {
        // eslint-disable-next-line no-unused-vars
        const fetchData = await fetchRequest(contactUrl, 'POST', JSON.stringify(data))
        dispatch({type: CONTACT_SUCCESS})
        onResetForm()

        setTimeout(() => dispatch(clearContactMessage()),3000)
    } catch (err) {
        dispatch(setContactError(err.message))
    }
}

export const setContactError = (payload) => dispatch => {
    dispatch(setError(payload,CONTACT_FAIL))
}

export const clearContactError = () => dispatch => {
    dispatch({type: CLEAR_CONTACT_ERROR})
}

export const clearContactMessage = () => dispatch => {
    dispatch({type: CLEAR_CONTACT_MESSAGE})
}

