import React, {useEffect} from 'react';
import InputDef from "../../layout/InputDef/InputDef";
import FormDef from "../../layout/FormDef/FormDef";
import {useDispatch, useSelector} from "react-redux";
import {useFormValue} from "../../../hooks/useFormValue";
import {clearContactError, clearContactMessage, contact, setContactError} from "../../../redux/action/contact";

function ContactForm() {
    const dispatch = useDispatch()
    const {error, success,loading} = useSelector(state => state.contact)
    const {formData, onChange, onResetForm, onPhoneChange} = useFormValue({
        first_name: '',
        last_name: '',
        email: '',
        phone: '',
        message: '',
    }, setContactError, error)

    useEffect(() => {
        return () => {
            error && dispatch(clearContactError())
            success && dispatch(clearContactMessage())
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const onSubmit = (e) => {
        e.preventDefault()
        dispatch(contact(formData, onResetForm))
    }

    const errorProp = error && typeof error !== 'object' ? error : null
    return (
        <FormDef
            onSubmit={onSubmit}
            errorProp={errorProp}
            successProp={success}
            successText={'Message sent successfully'}
            loading={loading}
        >
            <InputDef
                name='first_name'
                value={formData.first_name}
                errorProp={error?.first_name}
                onChange={onChange}
                type="text"
                placeholder={<>First Name<sup>*</sup></>}
            />
            <InputDef
                name='last_name'
                value={formData.last_name}
                errorProp={error?.last_name}
                onChange={onChange}
                type="text"
                placeholder={<>Last Name<sup>*</sup></>}
            />
            <InputDef
                name='email'
                value={formData.email}
                errorProp={error?.email}
                onChange={onChange}
                type="text"
                placeholder={<>Email<sup>*</sup></>}
            />
            <InputDef
                name='phone'
                value={formData.phone}
                errorProp={error?.phone}
                onChange={onPhoneChange}
                type="tel"
                placeholder={<>Phone<sup>*</sup></>}
            />
            <InputDef
                name='message'
                value={formData.message}
                errorProp={error?.message}
                onChange={onChange}
                isTextArea={true}
                placeholder={<>Message<sup>*</sup></>}
            />
        </FormDef>
    );
}

export default ContactForm;